<template>
    <tr>
    <td>{{produk.id}}</td>
    <td>{{produk.nama_produk}}</td>
    <td>{{produk.harga}}</td>
    <td>{{produk.kondisi}}</td>
    <td>
    <div class="ui mini buttons">
  <button class=" ui primary button" @click="onEdit"><i class="edit icon"></i></button>
  <div class=""></div>
  <button class=" ui red button"  @click="onDelete"><i class="times icon"></i> </button>
    </div>
   </td>
    </tr>
</template>
<script>
export default {
    name:"Produk",
    props:{
        produk:{
            type:Object
        }
    },
    methods: {
        onDelete(){
            // window.console.log("berhasil jon" +this.produk.id);
            this.$emit("onDelete", this.produk.id);
        },
          onEdit(){
            // window.console.log("edittt jon" +this.produk.id);
            this.$emit("onEdit", this.produk);
        }
    }
};
</script>

