<template>
  <div class="ui stackable menu">
    <template v-if="authenticated">
      <div class="item">
        <img src="vue.png" />
      </div>
      <a class="item">
        <router-link :to="{name:'Home'}">
          <i class="home icon"></i> Home
        </router-link>
      </a>
      <a class="item">
        <router-link :to="{name:'Info'}">
          <i class="address book icon icon"></i> Info
        </router-link>
      </a>
      <a class="item right" href="#" @click="signOut()">
        <i class="sign-out icon"></i> Sign Out
      </a>
    </template>
    <template v-else></template>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Nav",
  computed: {
    ...mapGetters({
      authenticated: "auth/authenticated",
      user: "auth/user"
    })
  },
  methods: {
    ...mapActions({
      signOutAction: "auth/signout"
    }),
    signOut() {
      this.signOutAction().then(() => this.$router.replace({ name: "signin" }));
    }
  }
};
</script>

<style scoped>
tac.kable menu {
  background-color: #00b5ad;
}
</style>