<template>
<div class="produk-list">
    <div class="data">
        <table class="ui single line table">
        <thead>
            <tr>           
            <th>ID</th>
            <th>Nama produk</th>
            <th>Harga</th>
            <th>Kondisi</th>
            <th style="width:148px;">Aksi</th>
            </tr>
        </thead>
        <tbody>
          <Produk 
          v-for="produk in produks" 
          :key="produk.id" 
          :produk="produk"
          @onDelete="onDelete"
          @onEdit="onEdit"
          />
        </tbody>
        </table>
</div>
</div>
</template>
<script>
import Produk from "./Produk"
export default {
  name: "ProdukList",
  components: {
    Produk
  },
  props: {
    produks: {
      type: Array
    }
  },
  methods: {
    onDelete(id) {
    //   window.console.log("customer list delete " + id);
      this.$emit("onDelete", id);
    },
       onEdit(data) {
    //   window.console.log("customer list delete " + id);
      this.$emit("onEdit", data);
    },
  }
};
</script>

<style scoped></style>