<template>
  <div class="my-form" style="textAlign:left">
    <form class="ui form">
      <div class="fields">
        <div class="four wide field">
          <label>Nama Produk</label>
          <input
            type="text"
            name="nama_produk"
            placeholder="Nama Produk"
            required
            @change="handleChange" 
            :value="form.nama_produk"
          />
        </div>

        <div class="four wide field">
          <label>Harga Produk</label>
          <input
            type="number"
            name="harga"
            placeholder="Harga"
            required
            @change="handleChange"
            :value="form.harga"
          />
        </div>
          <div class="two wide field">
          <label>Kondisi Produk</label>
          <select class="ui fluid dropdown"  name="kondisi" v-model="form.kondisi" required>
          <option value="baru">Baru</option>
          <option value="bekas">Bekas</option>
          </select>
      </div>
        
        <div class="two wide field">
          <button class="teal ui button submit-button" @click="onFormSubmit">
           <i class="check icon"></i>  Simpan
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Swal from "sweetalert2";

export default {
  name: "MyForm",
  props:{
    form:{
      type:Object
    }
  },
  methods:{
    handleChange(event){
      const{ name, value } = event.target;
      let form = this.form;
      form[name] = value;
      this.form = form;
    },

    // jalankan fungsi simpan produk

    onFormSubmit(event){
      event.preventDefault();

      if(this.formValidation()){

        // window.console.log("read to submit")
        this.$emit("onFormSubmit", this.form);

        this.clearFormFields();
      }
    },

    // cek jika form kosong

    formValidation(){
      if(document.getElementsByName('nama_produk')[0].value ===""){
         Swal.fire({
          icon: "error",
          title: "Nama Produk Harus Di isi !!"
        })
    
        return false;
      }

       if(document.getElementsByName('harga')[0].value ===""){  
          Swal.fire({
          icon: "error",
          title: "Harga Produk Harus Di isi !!"
        })
              return false;
            }
            
       if(document.getElementsByName('kondisi')[0].value ===""){
          Swal.fire({
          icon: "error",
          title: "Kondisi Produk Harus Di isi !!"
        })
              return false;
            }

            return true;
    },

    // bersihkan inputan form
    clearFormFields(){
      this.form.nama_produk = "";
      this.form.harga = "";
      this.form.kondisi = "";
      this.form.isEdit = false;

      document.querySelector(".form").reset();
    }
  }

}
</script>

<style scoped></style>