<template>
  <div id="app">
    <!-- panggil data  form dan produk  -->
    <div class="ui main container">
      <MyForm :form="form" @onFormSubmit="onFormSubmit" />
      <Loader v-if="loader" />
      <ProdukList :produks="produks" @onDelete="onDelete" @onEdit="onEdit" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import MyForm from "../components/MyForm";
import ProdukList from "../components/ProdukList";
import Loader from "../components/Loader";
import Swal from "sweetalert2";

export default {
  name: "Home",
  components: {
    MyForm,
    ProdukList,
    Loader
  },
  data() {
    return {
      url: "https://bl.ajisukmo.tech/api/produk",
      urldelete: "https://bl.ajisukmo.tech/api/delete",
      urlpost: "https://bl.ajisukmo.tech/api/tambahproduk",
      urledit: "https://bl.ajisukmo.tech/api/editproduk",

      produks: [],
      form: { nama_produk: "", harga: "", kondisi: "", isEdit: false },
      loader: false
    };
  },
  methods: {
    // ambil data produk
    getproduks() {
      this.loader = true;
      axios.get(this.url).then(data => {
        this.produks = data.data.data;
        this.loader = false;
      });
    },

    // hapus data produk
    deleteProduk(id) {
      this.loader = true;
      axios
        .delete(`${this.urldelete}/${id}`)
        .then(() => {
          this.getproduks();
        })
        .catch(e => {
          alert(e);
        });
    },

    // simpan data produk baru
    createProduk(data) {
      this.loader = true;

      axios
        .post(this.urlpost, {
          nama_produk: data.nama_produk,
          harga: data.harga,
          kondisi: data.kondisi
        })
        .then(() => this.getproduks())
        .catch(e => alert(e));
      Swal.fire({
        icon: "success",
        title: "Produk Berhasil Ditambahkan!"
      });
    },

    // edit data produk
    editProduk(data) {
      this.loader = true;
      axios
        .put(`${this.urledit}/${data.id}`, {
          nama_produk: data.nama_produk,
          harga: data.harga,
          kondisi: data.kondisi
        })
        .then(() => {
          this.getproduks();
        })
        .catch(e => {
          alert(e);
        });
      Swal.fire({
        icon: "success",
        title: "Produk Berhasil Diupdate!"
      });
    },

    // jalankan fungsi
    onDelete(id) {
      Swal.fire({
        title: "Apakah Anda Yakin?",
        text: "Ingin menghapus data id: " + id,
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "red",
        cancelButtonColor: "primary",
        confirmButtonText: "Delete"
      }).then(result => {
        if (result.value) {
          this.deleteProduk(id),
            Swal.fire("Deleted!", "Data Berhasil Di hapus.", "success");
        }
      });
    },
    onEdit(data) {
      // window.console.log("tes edit", data);

      this.form = data;
      this.form.isEdit = true;
    },
    onFormSubmit(data) {
      // window.console.log("tes submit", data)

      if (data.isEdit) {
        this.editProduk(data);
      } else {
        this.createProduk(data);
      }
    }
  },
  created() {
    this.getproduks();
  }
};
</script>

