<template>
  <div class="container">
    <div class="login">
      <center>
        <img src="vue.png" style="width:50%;" />
      </center>
      <form class="ui form align-center" @submit.prevent="submit">
        <div class="field">
          <label for="email">Email</label>
          <input type="email" name="email" id="email" autocomplete="off" v-model="form.email" />
        </div>
        <div class="field">
          <label for="password">Passsword</label>
          <input type="password" name="password" id="password" v-model="form.password" />
        </div>
        <button class="ui teal fluid button" type="submit">
          <i class="lock icon"></i> Login
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "SignIn",
  components: {},
  data() {
    return {
      form: {
        email: "",
        password: ""
      }
    };
  },
  methods: {
    ...mapActions({
      signIn: "auth/signIn"
    }),
    async submit() {
      this.signIn(this.form)
        .then(() => {
          this.$router.push({
            name: "Home"
          });
        })
        .catch(() => {
          console.log("failed");
        });
    }
  }
};
</script>

<style scoped>
.login {
  width: 50%;
  position: relative;
  margin: auto;
  margin-top: 1%;
}
@media screen and (min-width: 600px) {
  .login {
    width: 20%;
    position: relative;
    margin: auto;
    margin-top: 5%;
  }
}
</style>